import Vue from 'vue'
import VueResource from 'vue-resource'
import YmapPlugin from 'vue-yandex-maps'
require('./assets/map.scss')
Vue.use(VueResource)
Vue.use(YmapPlugin)

new Vue({
  el: '#shops-box',
  data: {
    layout: 'list',
    searchString: "",
    activeCity: "",
    shops: [],
    cities: [],
    mapFilter: ["pickpoint", "firm"],
    mapCenter: [],
    showLinks: null
  },
  computed: {
    filteredShops: function () {
      var shops_array = this.shops[this.activeCity] || [],
          searchString = this.searchString;

      for (var i = shops_array.length - 1; i >= 0; i--) {
        shops_array[i].balloonContent = '';
        shops_array[i].balloonContent += shops_array[i].address + "<br />";
        shops_array[i].balloonContent += '<i class=\"metro-icon\"></i>&nbsp;' + shops_array[i].metro + '<br />';
        shops_array[i].balloonContent += shops_array[i].phone + '<br />';
      }

      if (searchString){
        searchString = searchString.trim().toLowerCase();

        shops_array = shops_array.filter(function(item){
          if(item.name.toLowerCase().indexOf(searchString) !== -1){
            return item;
          }
        })
      }

      if (shops_array[0] != undefined) {
        this.mapCenter = [shops_array[0].latitude, shops_array[0].longitude];
      }

      //     .
      return shops_array;
    }
  },
  methods:{
    getShops: function(){
      this.$http.get('http://shop.dadget.ru/local/templates/dadget/include/offices.php').then(response => { 
        if(!!response.body) {
          this.shops = response.body
        }
      }, (response) => {
        this.shops = []
      });
    },
    getCities: function(){
      this.$http.get('http://shop.dadget.ru/local/templates/dadget/include/cities.php').then(response => { 
        if(!!response.body) {
          this.cities = response.body
        }
      }, (response) => {
        this.cities = []
      });
    },
    mapInitHandler: function(map){

    }
  },
  created: function() {
    this.getShops()
    this.getCities()
  }
})



